body {
  height: 100vh;
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  font-family: sans-serif;
  color: var(--tg-theme-text-color, #222);
  font-size: 14px;
  color-scheme: var(--tg-color-scheme);
}
body #root {
  height: 100%;
}
body #root .App {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body #root .App .wrapper {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}
body #root .App .wrapper .task-group {
  padding-top: 7px;
  background-color: var(--tg-theme-secondary-bg-color, #222);
  width: 100%;
  display: flex;
  height: 26px;
  justify-content: space-around;
  margin-bottom: -1px;
}
body #root .App .wrapper .task-group .button-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
body #root .App .wrapper .task-group .button-wrapper .half-sausage {
  width: 90%;
  height: 3px;
  background: 0;
  border-radius: 5px 5px 0 0;
}
body #root .App .wrapper .task-group .button-wrapper button {
  flex-basis: 33%;
  border: 0;
  background: 0;
  outline: 0;
}
body #root .App .wrapper > div:last-child {
  width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
body #root .App .wrapper > div:last-child .task-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  padding: 15px;
  overflow-y: scroll;
  height: calc(100% - 60px);
}
body #root .App .wrapper > div:last-child .send-button {
  display: block;
  width: 100%;
  font-size: 14px;
  margin: 0;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  background-color: var(--tg-theme-button-color, #50a8eb);
  color: var(--tg-theme-button-text-color, #ffffff);
  cursor: pointer;
}
body #root .App .text-field-wrapper {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}
body #root .App .text-field-wrapper .hiden-exit {
  width: 100%;
  height: 100%;
  z-index: 50;
  position: absolute;
}
body #root .App .text-field-wrapper .text-field {
  z-index: 100;
  display: flex;
  width: calc(100% - 30px);
  height: fit-content;
  padding: 20px 0;
  justify-content: center;
  align-items: center;
}
body #root .App .text-field-wrapper .text-field-inner {
  display: flex;
  background: var(--tg-theme-secondary-bg-color, #222);
  color: white;
  flex-direction: column;
  justify-content: space-between;
  width: 80%;
  height: fit-content;
  border-radius: 10px;
  overflow: hidden;
  background-color: #eaeaea;
}
body #root .App .text-field-wrapper .text-field-inner button {
  display: block;
  width: 100%;
  font-size: 14px;
  margin: 0;
  padding: 12px 20px;
  border: none;
  background-color: var(--tg-theme-button-color, #50a8eb);
  color: var(--tg-theme-button-text-color, #ffffff);
  cursor: pointer;
}
body #root .App .text-field-wrapper .text-field-inner input {
  flex-grow: 1;
  background: var(--tg-theme-secondary-bg-color, #222);
  box-sizing: border-box;
  width: 100%;
  min-height: 30px;
  height: fit-content;
  padding: 2px 5px;
  border: 0;
  outline: 0;
}
body #root .App .text-field-wrapper .text-field-inner input[type=text] {
  min-height: 50px;
  overflow-y: initial;
  text-wrap: initial;
}
body #root .App .text-field-wrapper .text-field-inner input:focus {
  padding: 2px 5px;
  outline: 0;
}
body #root .App .text-field-wrapper .text-field-inner h6 {
  height: fit-content;
  text-align: center;
  margin: 0;
  padding: 0;
  background: var(--tg-theme-secondary-bg-color, #222);
}
body #root .App .text-field-wrapper .text-field ul {
  display: flex;
  background: var(--tg-theme-secondary-bg-color, #222);
  box-sizing: border-box;
  justify-content: space-between;
  padding: 10px 5px;
  margin: 0;
}
body #root .App .text-field-wrapper .text-field ul > div {
  box-sizing: border-box;
  padding: 1px;
  max-width: 18px;
  flex-basis: 1fr;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
body #root .App .text-field-wrapper .text-field ul > div input {
  outline: 0;
  border: 0.5px;
  margin: 0;
}

