.task-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 10px 0;
}
.task-wrapper img {
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  background-color: #404040;
  border-radius: 50%;
  margin: 5px 5px 0 0;
}
.task-wrapper .task {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.task-wrapper .task-priority {
  width: 15px;
  height: 100%;
  background-color: red;
  border-radius: 15px 0 0 15px;
}
.task-wrapper .task-check-box {
  box-sizing: border-box;
  width: 20px;
  height: 100%;
  background: linear-gradient(180deg, rgb(40, 75, 255) 0%, rgb(96, 169, 255) 100%);
  border-left: 0;
  border-radius: 0 15px 15px 0;
}
.task-wrapper .task-check-box:hover {
  background: linear-gradient(180deg, rgb(0, 255, 55) 0%, rgb(3, 159, 0) 100%);
}
.task-wrapper .task .task-inner {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  box-sizing: border-box;
  padding: 10px;
  --task-bg-color: var(--tg-theme-secondary-bg-color);
  background-color: var(--task-bg-color, #404040);
}
.task-wrapper .task .task-inner input {
  min-width: 25px;
  min-height: 25px;
  border-radius: 5px;
  background-color: #404040;
}
.task-wrapper .task .task-inner p {
  height: auto;
  width: 90%;
  text-wrap: normal;
  text-align: start;
  margin: 0;
}

