.task-wrapper{
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 10px 0;

  img{
    width: 40px;
    height: 40px;
    min-width: 40px;
    min-height: 40px;

    background-color: #404040;
    border-radius: 50%;
    margin: 5px 5px 0 0;
  }


  .task{
    display: flex;
    justify-content: space-between;
    width: 100%;

    &-priority{
      width: 15px;
      height: 100%;
      background-color: red;
      border-radius: 15px 0 0 15px;
    }
    &-check-box{
      box-sizing: border-box;
      width: 20px;
      height: 100%;
      background: linear-gradient(180deg, rgba(40,75,255,1) 0%, rgba(96,169,255,1) 100%);

      &:hover{
        background: linear-gradient(180deg, rgba(0,255,55,1) 0%, rgba(3,159,0,1) 100%);

      }

      border-left: 0;
      border-radius: 0 15px 15px 0;
    }

    .task-inner{
      display: flex;
      width: 100%;
      height: 100%;
      align-items: center;
      box-sizing: border-box;
      padding: 10px;
      --task-bg-color: var(--tg-theme-secondary-bg-color);
      background-color: var(--task-bg-color, #404040);


      input{
        min-width: 25px;
        min-height: 25px;
        border-radius: 5px;
        background-color: #404040;
      }
      p{
        height: auto;
        width: 90%;
        text-wrap: normal;
        text-align: start;
        margin: 0;

      }
    }
  }
}